body {
  padding: 20px;
}

.content {
  max-width: 700px;
}

.img {
  width: 100%;
  height: auto;
}

.breed-select {
  max-width: 200px;
  margin-bottom: 20px;
}

.loader {
  width: 200px;
  height: 20px;
  background-color: rgba(0, 0, 0, .267);
  border-radius: 50px;
  margin: 0;
  position: relative;
  box-shadow: inset 0 0 0 2px rgba(0, 0, 0, .05);
}

.loader:after {
  content: "";
  background-color: #fff;
  border-radius: 50px;
  animation: slide 2s linear infinite;
  position: absolute;
  top: 2px;
  bottom: 2px;
  left: 2px;
  right: 360px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, .2);
}

@keyframes slide {
  0% {
    left: 2px;
    right: 170px;
  }

  5% {
    left: 2px;
  }

  50% {
    left: 150px;
    right: 2px;
  }

  55% {
    right: 2px;
  }

  100% {
    left: 2px;
    right: 150px;
  }
}

